 
  .customer-overlay{
    max-width:480px;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    background-image:
    url(/src/images/cr/fom-mob.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: 
      url(/src/images/cr/fom-mob.png);
    } 

    @media  screen and (min-width: 768px) {
      max-width:768px;
      height: 100%;     
      margin-left: auto;
      margin-right: auto;
      background-image:
      url(/src/images/cr/fon-tab.png);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover; 

      @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        background-image: 
        url(/src/images/cr/fon-tab-2.png);
      }  
     }
         @media  screen and (min-width: 1200px) {
         max-width: 1600px;
         height: 100%;
         margin-left: auto;
         margin-right: auto;
         background-image: 
         url(/src/images/cr/fon-desk.png);
         background-repeat: no-repeat;
         background-position: center;
         background-size: cover;
           @media (min-device-pixel-ratio: 2),
           (min-resolution: 192dpi),
           (min-resolution: 2dppx) {
           background-image:
           url(/src/images/cr/fon-desk-2.png);
            }
         }
       }

  .container-customer {
    width:100%;
    height: 100%; 
    margin-left: auto;
    margin-right: auto;
    @include tablet {
      max-width: 768px;
      padding-left: 31px;
      padding-right: 32px;
      padding-top: 223px;
      padding-bottom: 383px;
    }  

    @include desktop {
      max-width: 1200px;
      padding-left: 116px;
      padding-right: 116px;
      padding-top: 456px;
      padding-bottom: 207px;
    }
    }
   .swiper-slide {
      margin-left: auto;
      margin-right: auto; 
      display: block;
      justify-content: center;
      text-align: center;

      &__icon {
        align-items: center;        
        object-fit: cover;
        margin-left: auto;
        margin-right: auto;
        margin-bottom:32px;

      } 

      &__text {
        @include font(400, 12, 2.55, 0.04);
         color: #FCFCFC;
         margin-bottom: 30px;
         margin-top: 0px;
         padding: 39px 34px 39px 34px;
         background-color: #84A178;
         border-radius: 10px ;
     
         font-family:'Montserrat';
         font-style: normal;
         text-align: center;
         @include tablet {
           @include font(500, 14, 1.87, 0.04);
           margin-bottom: 32px;
           padding: 28px 80px 29px 80px;
           border-radius: 24px; 


         }
         @include desktop {
          @include font(500, 16, 1.87, 0.04);
          padding: 39px 109px 47px 109px;
         }
     
        
         
     
       }

       &__name {
        @include font (500, 20, 1.85, 0.04);
        font-family: 'Montserrat';
        font-style: normal;
        text-align: center;
        color: #131313;
        margin-top: 0;
        margin-bottom: 14px;
          @include tablet {
            @include font(500, 16, 1.86, 0.04);
            margin-bottom: 32px;
            
 
          }  
    
      }
      
      
    }
   .swiper-pagination {
    position: static !important;
    text-align: center;
    transition: .3s opacity;
    transform: translate3d(0,0,0);
    z-index: 10;
  }
    .swiper-pagination-bullet {
      width: 12px !important;
      height: 12px !important;
      margin:0 9px !important;
      display: inline-block;
      border-radius: 50%;
      background: var(--swiper-pagination-bullet-inactive-color,#d9d9d9);
      opacity: var(--swiper-pagination-bullet-inactive-opacity);
    }
    .swiper-pagination-bullet-active{
        background: #84A178 !important;
        box-shadow: 0px 3px 1px rgb(0 0 0 / 10%),
                    0px 1px 2px rgb(0 0 0 / 8%),
                    0px 2px 2px rgb(0 0 0 / 12%);
      }
  